import type { MtAnos, MtCssClassMap, MtEspecialidades, MtProdutos } from './types';

export const generateClasses = (
  produto: MtProdutos | undefined | null,
  especialidade: MtEspecialidades | undefined | null,
  ano: MtAnos | undefined | null,
  cssClassMap: MtCssClassMap,
): MtCssClassMap => {
  return typeof produto === 'string' && produto.length > 0
    ? {
        [`mt-${produto}`]: true,
        [`mt-${especialidade}`]: true,
        [`mt-${ano}`]: true,
        ...cssClassMap,
      }
    : cssClassMap;
};
